import { cast } from 'mobx-state-tree';

import { PlaylistWithArtists as PlaylistWithArtistsResponse } from '@yandex-music-int/resource-collection';

import { artistResponseToModel } from '@/entities/artist/index.js';
import { playlistResponseToModel } from '@/entities/playlist/index.js';

import { PlaylistWithArtistsModel } from '../model/PlaylistWithArtists.js';

export const playlistWithArtistsResponseToModel = (
    playlistResponse: PlaylistWithArtistsResponse | null | undefined
): PlaylistWithArtistsModel => {
    return cast({
        ...playlistResponseToModel(playlistResponse),
        artists: playlistResponse?.artists?.map(artistResponseToModel) ?? []
    });
};
