import { cast, flow, getEnv, Instance, types } from 'mobx-state-tree';

import { HttpErrorException, HttpResponseStatusCode } from '@yandex-music-int/http-client';
import {
    UsersResourcePlaylistsParams,
    UsersResourcePlaylistWithTracksIds,
    PlaylistTrackId,
    TracksMetaParams,
    Track,
    PlaylistResourceParams,
    PlaylistWithTracksIds
} from '@yandex-music-int/resource-collection';

import { PlaylistMeta, playlistResponseToMetaModel } from '@/features/playlist/index.js';
import { trackWithArtistsResponseToModel } from '@/features/track/index.js';

import { LoadingState, StoreEnv } from '@/shared/lib/index.js';

import { updatePlaylistItemsState } from '../lib/updatePlaylistItemsState.js';

import { PlaylistItem, PlaylistItemModel } from './PlaylistItem.js';

const INITIAL_TRACKS_COUNT_TO_FETCH = 10;

export const Playlist = types
    .model('Playlist', {
        meta: types.maybeNull(PlaylistMeta),
        items: types.array(PlaylistItem),
        errorStatusCode: types.maybeNull(types.number),
        loadingState: types.enumeration(Object.values(LoadingState))
    })
    // Отключаем правило, так как сложно вывести тип
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            getTracks: flow(function* ({ trackIds }: TracksMetaParams): Generator<Promise<Track[]>, void, Track[]> {
                const { tracksResource, logger } = getEnv<StoreEnv>(self);

                try {
                    if (!self.meta?.uid || !self.meta?.kind) {
                        return;
                    }

                    updatePlaylistItemsState(trackIds, self.items, LoadingState.PENDING);

                    const data = yield tracksResource.getTracksMeta({
                        trackIds
                    });

                    self.items = cast(
                        self.items.map((item: PlaylistItemModel) => {
                            const trackData = data.find((track: Track) => String(track?.id) === item.id);

                            if (trackData) {
                                return {
                                    id: trackData.id,
                                    data: trackWithArtistsResponseToModel(trackData),
                                    loadingState: LoadingState.RESOLVE
                                };
                            }

                            return item;
                        })
                    );
                } catch (error) {
                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }
                    updatePlaylistItemsState(trackIds, self.items, LoadingState.REJECT);
                }
            })
        };
    })
    // Отключаем правило, так как сложно вывести тип
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            updateData: flow(function* (data: PlaylistWithTracksIds) {
                if (data?.error === 'not-found') {
                    self.errorStatusCode = HttpResponseStatusCode.NOT_FOUND;
                    self.loadingState = LoadingState.REJECT;
                    return;
                }

                self.meta = playlistResponseToMetaModel(data);

                const initialTrackIds: string[] = [];

                self.items = cast(
                    data.tracks.map((track: PlaylistTrackId, index: number) => {
                        if (index < INITIAL_TRACKS_COUNT_TO_FETCH) {
                            initialTrackIds.push(String(track.id));
                        }
                        return {
                            id: String(track.id),
                            loadingState: LoadingState.IDLE
                        };
                    })
                );

                return yield self.getTracks({ trackIds: initialTrackIds });
            })
        };
    })
    // Отключаем правило, так как сложно вывести тип
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            getPlaylistByUserIdAndKind: flow(function* ({
                userId,
                playlistKind,
                resumeStream = false,
                trackMetaType = 'music'
            }: UsersResourcePlaylistsParams): Generator<
                Promise<UsersResourcePlaylistWithTracksIds | unknown>,
                void,
                UsersResourcePlaylistWithTracksIds
            > {
                const { usersResource, logger } = getEnv<StoreEnv>(self);

                if (self.loadingState === LoadingState.PENDING) {
                    return;
                }

                try {
                    self.loadingState = LoadingState.PENDING;

                    const data = yield usersResource.getPlaylistWithTracksIds({
                        userId,
                        playlistKind,
                        resumeStream,
                        trackMetaType
                    });

                    if (typeof data.playlistUuid !== 'string') {
                        self.errorStatusCode = HttpResponseStatusCode.NOT_FOUND;
                        self.loadingState = LoadingState.REJECT;
                        return;
                    }

                    yield self.updateData(data);

                    self.loadingState = LoadingState.RESOLVE;
                } catch (error) {
                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }

                    if (
                        error instanceof HttpErrorException &&
                        (error.statusCode === HttpResponseStatusCode.NOT_FOUND ||
                            error.statusCode === HttpResponseStatusCode.BAD_REQUEST)
                    ) {
                        self.errorStatusCode = HttpResponseStatusCode.NOT_FOUND;
                    }

                    self.loadingState = LoadingState.REJECT;
                }
            }),
            getPlaylistByUuid: flow(function* ({
                playlistUuid,
                richTracks = false,
                resumeStream = false
            }: PlaylistResourceParams): Generator<
                Promise<UsersResourcePlaylistWithTracksIds | unknown>,
                void,
                UsersResourcePlaylistWithTracksIds
            > {
                const { playlistResource, logger } = getEnv<StoreEnv>(self);

                if (self.loadingState === LoadingState.PENDING) {
                    return;
                }

                try {
                    self.loadingState = LoadingState.PENDING;

                    const data = yield playlistResource.getPlaylist({
                        playlistUuid,
                        resumeStream,
                        richTracks
                    });

                    yield self.updateData(data);

                    // @ts-expect-error
                    // TS считает, что статусы никогда не пересекутся, но на самом деле мы можем уйти со страницы,
                    // раньше чем закончится последний ретрай и/или запрос, когда уже проставлен статус IDLE
                    if (self.loadingState !== LoadingState.IDLE) {
                        self.loadingState = LoadingState.RESOLVE;
                    }
                } catch (error) {
                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }

                    if (
                        error instanceof HttpErrorException &&
                        (error.statusCode === HttpResponseStatusCode.NOT_FOUND ||
                            error.statusCode === HttpResponseStatusCode.BAD_REQUEST)
                    ) {
                        self.errorStatusCode = HttpResponseStatusCode.NOT_FOUND;
                    }

                    if (self.loadingState !== LoadingState.IDLE) {
                        self.loadingState = LoadingState.REJECT;
                    }
                }
            }),
            reset() {
                self.meta = null;
                self.loadingState = LoadingState.IDLE;
                self.items = cast([]);
                self.errorStatusCode = null;
            }
        };
    })
    // Отключаем правило, так как сложно вывести тип
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get isNotFound(): boolean {
            return (
                self.loadingState === LoadingState.REJECT && self.errorStatusCode === HttpResponseStatusCode.NOT_FOUND
            );
        },
        get isLoading() {
            return self.loadingState === LoadingState.PENDING || self.loadingState === LoadingState.IDLE;
        },
        get isDisabled() {
            return self.loadingState === LoadingState.RESOLVE && (self.items.length === 0 || !self.meta?.available);
        }
    }));

export type PlaylistModel = Instance<typeof Playlist>;
