import { cast } from 'mobx-state-tree';

import { MadeFor } from '@yandex-music-int/resource-collection';

import { caseFormsResponseToModel } from '@/shared/lib/index.js';

import { type PlaylistMadeForModel } from '../model/PlaylistMadeFor.js';

import { playlistOwnerResponseToModel } from './playlistOwnerResponseToModel.js';

export const playlistMadeForResponseToModel = (response: MadeFor): PlaylistMadeForModel => {
    return cast({
        userInfo: response.userInfo ? playlistOwnerResponseToModel(response.userInfo) : null,
        caseForms: response.caseForms ? caseFormsResponseToModel(response.caseForms) : null
    });
};
