'use client';

import { forwardRef, ReactElement, Ref } from 'react';

import { WithClassName } from '@/shared/lib/index.js';
import { CarouselBlock } from '@/shared/ui/index.js';

import { PlaylistWithArtistsModel } from '../../model/PlaylistWithArtists.js';
import { PlaylistCard } from '../PlaylistCard/PlaylistCard.js';

interface Props extends WithClassName {
    forwardRef?: Ref<HTMLDivElement>;
    title: string;
    description?: string;
    playlists?: PlaylistWithArtistsModel[];
    isLoading?: boolean;
    containerClassName?: string;
    headerClassName?: string;
}

const PlaylistsCarouselComponent = ({
    forwardRef: ref,
    title,
    description,
    playlists,
    isLoading,
    containerClassName = '',
    className = '',
    headerClassName
}: Props): ReactElement | null => {
    return (
        <CarouselBlock
            className={className}
            headerClassName={headerClassName}
            containerClassName={containerClassName}
            isLoading={isLoading}
            ref={ref}
            title={title}
            description={description}
        >
            {playlists?.map((playlist: PlaylistWithArtistsModel) => {
                return <PlaylistCard key={playlist.key} playlist={playlist} contentLinesCount={3} />;
            })}
        </CarouselBlock>
    );
};

export const PlaylistsCarousel = forwardRef<HTMLDivElement, Props>((props: Props, ref: Ref<HTMLDivElement>) => {
    return <PlaylistsCarouselComponent forwardRef={ref} {...props} />;
});
