import { TrackId } from '@yandex-music-int/resource-collection';

import { LoadingState } from '@/shared/lib/index.js';

import { PlaylistItemModel } from '../model/PlaylistItem.js';

/**
 * Функция для изменения состояния у определенных треков
 * @param trackIds Массив id треков, у которых необходимо поменять состояние
 * @param items Массив элементов для виртуального скролла
 * @param state Новое состояние для конкретного трека
 */
export const updatePlaylistItemsState = (
    trackIds: TrackId[],
    items: PlaylistItemModel[],
    state: LoadingState
): void => {
    items.forEach((item: PlaylistItemModel) => {
        if (trackIds.includes(item.id)) {
            item = {
                id: item.id,
                data: null,
                loadingState: state
            };
        }
    });
};
