import { Instance, types } from 'mobx-state-tree';

import { Playlist } from '@/entities/playlist/index.js';

import { PlaylistMadeFor } from './PlaylistMadeFor.js';
import { PlaylistOwner } from './PlaylistOwner.js';

export const PlaylistMeta = Playlist.props({
    owner: types.maybe(PlaylistOwner),
    modified: types.string,
    description: types.maybe(types.string),
    madeFor: types.maybeNull(PlaylistMadeFor)
});

export type PlaylistMetaModel = Instance<typeof PlaylistMeta>;
