import { Instance, types } from 'mobx-state-tree';

import { TrackWithArtists } from '@/features/track/index.js';

import { LoadingState } from '@/shared/lib/index.js';

export const PlaylistItem = types.model('PlaylistItem', {
    id: types.union(types.string, types.number),
    data: types.maybeNull(TrackWithArtists),
    loadingState: types.enumeration(Object.values(LoadingState))
});

export type PlaylistItemModel = Instance<typeof PlaylistItem>;
