import { cast } from 'mobx-state-tree';

import { Playlist } from '@yandex-music-int/resource-collection';

import { playlistResponseToModel } from '@/entities/playlist/index.js';

import { type PlaylistMetaModel } from '../model/PlaylistMeta.js';

import { playlistMadeForResponseToModel } from './playlistMadeForResponseToModel.js';
import { playlistOwnerResponseToModel } from './playlistOwnerResponseToModel.js';

export const playlistResponseToMetaModel = (response: Playlist): PlaylistMetaModel => {
    return cast({
        ...playlistResponseToModel(response),
        owner: response.owner ? playlistOwnerResponseToModel(response.owner) : undefined,
        description: response.description,
        modified: response.modified,
        madeFor: response.madeFor ? playlistMadeForResponseToModel(response.madeFor) : null
    });
};
