'use client';

import { observer } from 'mobx-react-lite';
import { notFound } from 'next/navigation.js';
import { ReactElement, use, useCallback, useMemo, useEffect, CSSProperties } from 'react';
import { ListRange } from 'react-virtuoso';

import { YaMusicContexts } from '@yandex-music-int/player-contexts';
import { useElementOffsetY } from '@yandex-music-int/sol/common/hooks/useElementOffsetY.js';

import { Header } from '@/widgets/content/index.js';
import { PageHeaderShimmer, PageHeaderPlaylist } from '@/widgets/page-header/index.js';

import { TrackPlaylist } from '@/features/track/index.js';

import { hasDuplicatesBefore } from '@/entities/playlist/index.js';

import {
    CONTAINER_COOKIE_STORAGE,
    LoadingState,
    TrackVariant,
    getAverageBackgroundColor,
    useContainer,
    useStore,
    useRumPage,
    AnalyticsPageProvider,
    useCreateFrom,
    AnalyticsPage,
    AnalyticsBlock
} from '@/shared/lib/index.js';
import { Footer, TrackShimmer, VirtualScroll } from '@/shared/ui/index.js';

import { PlaylistItemModel } from '../../model/PlaylistItem.js';

import styles from './Playlist.module.scss';

const DEBOUNCE_DURATION_MS = 300;

interface Props {
    playlistUuid?: string;
    userId?: string;
    kind?: string;
}

const HEADER_TO_VIEWPORT_OFFSET_NEW_NAVIGATION = 93;
const HEADER_TO_VIEWPORT_OFFSET_OLD_NAVIGATION = 17;

export const PlaylistPage = observer(({ userId, kind, playlistUuid }: Props): ReactElement => {
    const from = useCreateFrom({ pageId: AnalyticsPage.PLAYLIST, blockId: AnalyticsBlock.PLAYLIST });
    const container = useContainer();
    const cookies = container.get(CONTAINER_COOKIE_STORAGE);
    let isWebNextNavigationOn = false;

    if (process.env.BUILD_TYPE === 'web') {
        isWebNextNavigationOn = cookies.get('WebNextNavigation') === 'on';
    }

    const { playlist } = useStore();

    const averageColor = useMemo(() => {
        return getAverageBackgroundColor(playlist?.meta?.averageColor);
    }, [playlist?.meta?.averageColor]);

    const { ref, offsetY } = useElementOffsetY();

    const headerStyle = useMemo(() => {
        if (offsetY !== undefined) {
            const initialTranslateHeader = isWebNextNavigationOn
                ? HEADER_TO_VIEWPORT_OFFSET_NEW_NAVIGATION
                : HEADER_TO_VIEWPORT_OFFSET_OLD_NAVIGATION;
            const translate = offsetY - initialTranslateHeader;
            return {
                '--average-color-background': averageColor,
                transform: `translateY(${offsetY >= initialTranslateHeader ? 0 : translate}px)`,
                opacity: 1
            };
        }
        return {};
    }, [offsetY, isWebNextNavigationOn, averageColor]);

    useEffect(() => {
        return () => {
            if (playlist.uid !== userId || playlist.kind !== kind || playlist.uuid !== playlistUuid) {
                playlist.reset();
            }
        };
    }, [playlist, userId, kind, playlistUuid]);

    if (playlist.loadingState === LoadingState.IDLE) {
        if (typeof playlistUuid === 'string') {
            use(
                playlist.getPlaylistByUuid({
                    playlistUuid
                })
            );
        } else {
            use(
                playlist.getPlaylistByUserIdAndKind({
                    userId,
                    playlistKind: kind
                })
            );
        }
    }

    useRumPage(playlist.loadingState === LoadingState.RESOLVE);

    if (playlist.isNotFound) {
        notFound();
    }

    const playlistHeader = useMemo(() => {
        if (playlist.isLoading) {
            return <PageHeaderShimmer className={styles.header} />;
        }
        return (
            <PageHeaderPlaylist
                playlist={playlist.meta}
                isDisabled={playlist.isDisabled}
                className={styles.header}
                ref={ref}
            />
        );
    }, [playlist.isDisabled, playlist.isLoading, playlist.meta, ref]);

    const customComponents = useMemo(() => {
        return {
            Header: () => {
                return (
                    <>
                        <Header />
                        {playlistHeader}
                    </>
                );
            },

            Footer: () => <Footer className={styles.footer} />
        };
    }, [playlistHeader]);

    const getPlaylistTracksByRange = useCallback(
        (range: ListRange) => {
            const trackIds = [];
            for (let i = range.startIndex; i <= range.endIndex; i++) {
                const item = playlist.items[i] as PlaylistItemModel;
                if (item?.loadingState === LoadingState.IDLE) {
                    trackIds.push(item.id);
                }
            }
            if (trackIds.length) {
                playlist.getTracks({
                    trackIds
                });
            }
        },
        [playlist]
    );

    const itemContentCallback = (index: number): ReactElement | undefined => {
        const item = playlist?.items?.[index] as PlaylistItemModel;

        if (!item || !item?.data) {
            return <TrackShimmer className={styles.shimmerItem} variant={TrackVariant.PLAYLIST} />;
        }

        return (
            <TrackPlaylist
                track={item.data}
                hasDuplicatesBefore={hasDuplicatesBefore(playlist?.items, item.data, index)}
                playContextParams={{
                    contextData: {
                        type: YaMusicContexts.Playlist,
                        meta: {
                            id: playlist.meta.uuid
                        },
                        from
                    },
                    queueParams: {
                        index
                    },
                    loadContextMeta: true
                }}
            />
        );
    };

    return (
        <AnalyticsPageProvider pageId={AnalyticsPage.PLAYLIST}>
            <div className={styles.averageColorBackground} style={headerStyle as CSSProperties} />
            <VirtualScroll
                className={styles.root}
                listClassName={styles.content}
                customComponents={customComponents}
                totalCount={playlist.items.length}
                // Для корректного обновления виртуального скролла
                // eslint-disable-next-line react/jsx-no-bind
                itemContentCallback={itemContentCallback}
                onGetDataByRange={getPlaylistTracksByRange}
                debounceDurationInMs={DEBOUNCE_DURATION_MS}
                initialItemCount={playlist.items.length}
            />
        </AnalyticsPageProvider>
    );
});
