import { types, Instance } from 'mobx-state-tree';

import { OwnerSex } from '@yandex-music-int/resource-collection';

export const PlaylistOwner = types.model('PlaylistOwner', {
    uid: types.number,
    login: types.string,
    name: types.string,
    sex: types.enumeration(Object.values(OwnerSex)),
    verified: types.boolean
});

export type PlaylistOwnerModel = Instance<typeof PlaylistOwner>;
