'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import React, { MouseEvent, ReactElement, useCallback, useMemo, useState } from 'react';
import { useIntl } from 'react-intl';

import { YaMusicContexts } from '@yandex-music-int/player-contexts';
import { Button } from '@yandex-music-int/sol/components/Button';
import { EntityCard, EntityCardProps, CommonCardControls } from '@yandex-music-int/sol/components/EntityCard';
import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Image } from '@yandex-music-int/sol/components/Image';
import { Paper } from '@yandex-music-int/sol/components/Paper';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { SeparatedArtists } from '@/entities/artist/index.js';
import { useOnLikeClick } from '@/entities/playlist/index.js';

import { useCreateFrom, useSearchFeedback, WithChildren, WithClassName, usePlayableContext } from '@/shared/lib/index.js';
import { CardLikes, Link, PlayButton, useFuture, LikeButton } from '@/shared/ui/index.js';

import { PlaylistWithArtistsModel } from '../../model/PlaylistWithArtists.js';

import styles from './PlaylistCard.module.scss';

export interface Props extends WithClassName, WithChildren {
    playlist: PlaylistWithArtistsModel;
    customDescription?: string;
    contentLinesCount?: EntityCardProps['contentLinesCount'];
}

export const PlaylistCard = observer(
    ({ className, playlist, children, contentLinesCount, customDescription }: Props): ReactElement => {
        const from = useCreateFrom();
        const { isFuture } = useFuture();
        const { formatMessage } = useIntl();
        const handleOnLikeClick = useOnLikeClick(playlist);
        const { sendLikeSearchFeedback, sendNavigateSearchFeedback, sendPlaySearchFeedback } = useSearchFeedback();

        const playlistAriaLabel = useMemo(() => {
            const likesLabel =
                typeof playlist.actualLikesCount === 'number'
                    ? playlist.actualLikesCount > 0
                        ? formatMessage({ id: 'entity-names.likes-counter' }, { counter: playlist.actualLikesCount })
                        : formatMessage({ id: 'entity-names.likes-counter-empty' })
                    : '';
            const playlistName = formatMessage({ id: 'entity-names.playlist-name' }, { playlistName: playlist.title });

            return `${playlistName} ${likesLabel}`;
        }, [formatMessage, playlist.title, playlist.actualLikesCount]);

        const { isPlaying, togglePlay } = usePlayableContext({
            playContextParams: {
                contextData: {
                    type: YaMusicContexts.Playlist,
                    meta: {
                        id: playlist.uuid
                    },
                    from
                },
                loadContextMeta: true
            }
        });

        //TODO: заменить на внутреннюю обработку в MenuButton
        const handleClick = useCallback((event: MouseEvent<HTMLButtonElement>) => {
            event.stopPropagation();
            event.preventDefault();
        }, []);

        const handleNavigate = useCallback(() => {
            sendNavigateSearchFeedback?.();
        }, [sendNavigateSearchFeedback]);

        const [onSearchFeedbackPlayed, setOnSearchFeedbackPlayed] = useState(false);
        const handlePlayButtonClick = useCallback(() => {
            if (!onSearchFeedbackPlayed && !isPlaying) {
                setOnSearchFeedbackPlayed(true);
                sendPlaySearchFeedback?.();
            }

            togglePlay();
        }, [isPlaying, onSearchFeedbackPlayed, sendPlaySearchFeedback, togglePlay]);

        const [onSearchFeedbackLiked, setOnSearchFeedbackLiked] = useState(false);
        const handleLikeButtonClick = useCallback(() => {
            if (!onSearchFeedbackLiked && !playlist.isLiked) {
                setOnSearchFeedbackLiked(true);
                sendLikeSearchFeedback?.();
            }

            handleOnLikeClick();
        }, [handleOnLikeClick, onSearchFeedbackLiked, playlist.isLiked, sendLikeSearchFeedback]);

        const menuControl = useMemo(() => {
            if (isFuture) {
                return (
                    <Button
                        className={clsx(styles.icon, styles.control)}
                        //TODO: Заменить на релевантное имя, когда будет готово контекстное меню
                        aria-label={formatMessage({ id: 'interface-actions.context-menu' })}
                        icon={<Icon size="xxs" variant="more" />}
                        size="s"
                        radius="round"
                        onClick={handleClick}
                    />
                );
            }

            return null;
        }, [isFuture, handleClick, formatMessage]);

        const description = useMemo(() => {
            if (customDescription) {
                return (
                    <Caption variant="span" type="entity" size="s" weight="medium" lineClamp={2}>
                        {customDescription}
                    </Caption>
                );
            }

            if (playlist.artists) {
                return (
                    <SeparatedArtists
                        className={styles.artists}
                        artists={playlist.artists}
                        lineClamp={1}
                        linkClassName={styles.artistLink}
                        captionSize="s"
                    />
                );
            }

            return;
        }, [playlist.artists, customDescription]);

        const view = useMemo(() => {
            return (
                <Paper className={styles.cover} radius="s" withShadow>
                    <Link
                        className={styles.coverLink}
                        href={playlist.url}
                        aria-label={playlist.title}
                        onClick={handleNavigate}
                    >
                        <Image
                            className={styles.image}
                            src={playlist.coverUri}
                            size={200}
                            fit="cover"
                            alt={playlistAriaLabel}
                            withAvatarReplace
                        />
                        <CommonCardControls
                            className={styles.controls}
                            playControl={
                                <PlayButton
                                    className={clsx(styles.playButton, styles.control)}
                                    buttonVariant="default"
                                    withHover={false}
                                    iconSize="l"
                                    variant="filled"
                                    onClick={handlePlayButtonClick}
                                    isPlaying={isPlaying}
                                />
                            }
                            likeControl={!playlist.isFavouritePlaylist && (
                                    <LikeButton
                                        isLiked={playlist.isLiked}
                                        onClick={handleLikeButtonClick}
                                        variant="default"
                                        size="s"
                                        iconSize="xxs"
                                        className={styles.control}
                                    />
                                )}
                            menuControl={menuControl}
                        />
                    </Link>
                </Paper>
            );
        }, [
            playlist.url, 
            playlist.title, 
            playlist.coverUri, 
            playlist.isFavouritePlaylist, 
            playlist.isLiked, handleNavigate, 
            playlistAriaLabel, 
            handlePlayButtonClick, 
            isPlaying, 
            handleLikeButtonClick, 
            menuControl
        ]);

        return (
            <EntityCard
                aria-label={playlistAriaLabel}
                className={clsx(styles.root, className)}
                title={
                    <Caption variant="div" type="entity" size="s" weight="medium" lineClamp={2}>
                        <Link className={styles.titleLink} href={playlist.url} onClick={handleNavigate}>
                            {playlist.title}
                        </Link>
                    </Caption>
                }
                contentLinesCount={contentLinesCount}
                view={view}
                description={description}
            >
                {Boolean(playlist.actualLikesCount) && (
                    <CardLikes
                        likesCount={playlist.actualLikesCount}
                        isLiked={playlist.isLiked}
                        handleOnLikeClick={handleOnLikeClick}
                    />
                )}
                {children}
            </EntityCard>
        );
    }
);
