'use client';

import { notFound, useSearchParams } from 'next/navigation.js';
import { ReactElement } from 'react';

import { SecondaryPage } from '@/app/navigation/index.js';

import { PlaylistPage } from '@/pages/playlist/index.js';

const UsersPage = (): ReactElement | void => {
    const searchParams = useSearchParams();

    const playlistUuid = searchParams.get('playlistUuid');

    if (!playlistUuid) {
        notFound();
    }

    return (
        <SecondaryPage>
            <PlaylistPage playlistUuid={playlistUuid} />
        </SecondaryPage>
    );
};

// eslint-disable-next-line import/no-default-export
export default UsersPage;
